				<div class="modal-header">
					<h5 class="modal-title">Adresse bearbeiten</h5>
				</div>
				<div class="modal-body">
					<form class="mb-3">
						<label class="form-label">Neue Adresse angeben</label>
						<div class="row row-space-10">
							<div class="col-9">
								<input type="text" class="form-control" placeholder="Straße" required />
							</div>
							<div class="col-3">
								<input type="number" class="form-control" placeholder="Nr." required />
							</div>
						</div>
						<div class="row row-space-10 mt-2">
							<div class="col-4">
								<input type="number" class="form-control" placeholder="PLZ" required />
							</div>
							<div class="col-8">
								<input type="text" class="form-control" placeholder="Stadt" required />
							</div>
						</div>
						<div class="row row-space-10 mt-2">
							<div class="col-7 form-group">
								<label for="stateSelect">Bundesländer auswählen</label>
								<select class="form-select" id="stateSelect" v-model="selectedState">
									<option v-if="!selectedCountry" value="" disabled>Keine Bundesländer verfügar
									</option>
									<option v-else v-for="state in availableStates" :key="state" :value="state">
										{{ state }}
									</option>
								</select>
							</div>
							<div class="col-5 form-group">
								<label for="countrySelect">Land auswählen</label>
								<select class="form-select" id="countrySelect" v-model="selectedCountry">
									<option v-if="!selectedCountry" value="" disabled>-</option>
									<option v-for="(countryName, countryCode) in countries" :key="countryCode"
										:value="countryCode">
										{{ countryName }}
									</option>
								</select>
							</div>
						</div>
					</form>
					<div class="alert bg-body">
