						<div class="btn-group" role="group" aria-label="Radio toggle for dark mode setting">
							<input type="radio" v-model="darkMode" value="off" class="btn-check" name="btnradio"
								id="btnradioon" autocomplete="off">
							<label class="btn btn-outline-secondary" for="btnradioon">Ausgeschaltet</label>

							<input type="radio" v-model="darkMode" value="auto" class="btn-check" name="btnradio"
								id="btnradioauto" autocomplete="off" checked>
							<label class="btn btn-outline-primary" for="btnradioauto">Automatisch</label>

							<input type="radio" v-model="darkMode" value="on" class="btn-check" name="btnradio"
								id="btnradiooff" autocomplete="off">
							<label class="btn btn-outline-secondary" for="btnradiooff">Eingeschaltet</label>
						</div>
					</div>
					<div class="mt-2 alert bg-body">
						<b>Information:</b>
