<script>
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import Cookies from 'js-cookie';

export default {
  components: { Datepicker },
  props: {
    rangeMode: {
      type: Boolean,
      default: false
    },
    multiCalendars: {
      type: Boolean,
      default: false
    },
    monthPicker: {
      type: Boolean,
      default: false
    },
    timePicker: {
      type: Boolean,
      default: false
    },
    yearPicker: {
      type: Boolean,
      default: false
    },
    weekPicker: {
      type: Boolean,
      default: false
    },
    clockVisible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      date: null
    };
  },
  computed: {
    locale() {
      return Cookies.get('uslang') === "de" ? "de" : "en"; // Hier kannst du eine alternative Locale angeben
    },
    getFormat() {
      return this.locale === "de" ? "dd.MM.yyyy" : "MM/dd/yyyy";
    },
    selectTxt() {
      return this.$t('GENERAL.SELECT');
    },
    cancelTxt() {
      return this.$t('GENERAL.CANCEL');
    },
    datepickerProps() {
      return {
        range: this.rangeMode,
        multiCalendars: this.multiCalendars,
        monthPicker: this.monthPicker,
        timePicker: this.timePicker,
        yearPicker: this.yearPicker,
        weekPicker: this.weekPicker
      };
    }
  }
};
</script>

<template>
  <Datepicker 
    v-model="date" 
    v-bind="datepickerProps" 
    :locale="locale"
    :format="getFormat" 
    :select-text="selectTxt"
    :cancel-text="cancelTxt" 
    :enable-time-picker="clockVisible" 
    dark>
  </Datepicker>
</template>
